import React, { useState, useEffect } from "react";
import {
  Mail,
  Lock,
  Heart,
  Moon,
  Sun,
  Eye,
  EyeOff,
  User,
  Phone,
  Calendar,
  Drop,
  AlertCircle,
} from "lucide-react";

const DonorRegistration = () => {
  const [formData, setFormData] = useState({
    firstName: "",
    lastName: "",
    email: "",
    password: "",
    confirmPassword: "",
    phone: "",
    dateOfBirth: "",
    bloodType: "",
    weight: "",
    lastDonation: "",
    medicalConditions: "",
    address: "",
  });

  const [focusedInput, setFocusedInput] = useState("");
  const [isDarkMode, setIsDarkMode] = useState(false);
  const [showPassword, setShowPassword] = useState(false);
  const [emailError, setEmailError] = useState("");
  const [passwordErrors, setPasswordErrors] = useState({
    length: false,
    number: false,
    uppercase: false,
    lowercase: false,
    special: false,
  });
  const [passwordMatch, setPasswordMatch] = useState(true);

  const bloodTypes = ["A+", "A-", "B+", "B-", "AB+", "AB-", "O+", "O-"];

  const validateEmail = (email) => {
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    if (!email) {
      setEmailError("Email is required");
    } else if (!emailRegex.test(email)) {
      setEmailError("Please enter a valid email address");
    } else {
      setEmailError("");
    }
  };

  const validatePassword = (password) => {
    setPasswordErrors({
      length: password.length >= 8 && password.length <= 12,
      number: /\d/.test(password),
      uppercase: /[A-Z]/.test(password),
      lowercase: /[a-z]/.test(password),
      special: /[!@#$%^&*(),.?":{}|<>]/.test(password),
    });
  };

  useEffect(() => {
    if (formData.email) validateEmail(formData.email);
  }, [formData.email]);

  useEffect(() => {
    validatePassword(formData.password);
  }, [formData.password]);

  useEffect(() => {
    setPasswordMatch(formData.password === formData.confirmPassword);
  }, [formData.password, formData.confirmPassword]);

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    const isPasswordValid = Object.values(passwordErrors).every(Boolean);
    if (!emailError && isPasswordValid && passwordMatch) {
      console.log("Registration attempted with:", formData);
    }
  };

  const toggleDarkMode = () => {
    setIsDarkMode(!isDarkMode);
  };

  const InputField = ({ icon: Icon, name, label, type = "text", ...props }) => (
    <div className="relative rounded-lg shadow-sm">
      <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
        <Icon
          className={`h-5 w-5 ${
            focusedInput === name ? "text-red-500" : "text-gray-400"
          }`}
        />
      </div>
      <input
        name={name}
        type={type}
        value={formData[name]}
        onChange={handleInputChange}
        onFocus={() => setFocusedInput(name)}
        onBlur={() => setFocusedInput("")}
        className={`block w-full pl-10 pr-3 py-3 ${
          isDarkMode ? "bg-gray-700 text-white" : "bg-white text-gray-900"
        } rounded-lg focus:outline-none focus:ring-2 focus:ring-red-500 focus:ring-opacity-50 transition-all duration-200`}
        {...props}
      />
    </div>
  );

  return (
    <div
      className={`min-h-screen w-full ${
        isDarkMode
          ? "bg-gradient-to-br from-gray-900 via-gray-800 to-gray-900"
          : "bg-gradient-to-br from-red-50 via-white to-red-50"
      } py-12 px-4 sm:px-6 lg:px-8`}
    >
      <button
        onClick={toggleDarkMode}
        className="fixed top-4 right-4 p-2 rounded-full bg-white/80 backdrop-blur-sm shadow-lg hover:bg-gray-100 transition-colors duration-200"
      >
        {isDarkMode ? (
          <Sun className="h-6 w-6 text-yellow-500" />
        ) : (
          <Moon className="h-6 w-6 text-gray-800" />
        )}
      </button>

      <div className="max-w-2xl mx-auto">
        <div className="flex justify-center">
          <Heart
            className={`w-16 h-16 ${
              isDarkMode ? "text-red-600" : "text-red-500"
            } drop-shadow-lg`}
          />
        </div>

        <h2
          className={`mt-6 text-center text-4xl font-bold ${
            isDarkMode ? "text-white" : "text-gray-900"
          } drop-shadow-sm`}
        >
          Donor Registration
        </h2>

        <p
          className={`mt-2 text-center text-lg ${
            isDarkMode ? "text-gray-300" : "text-gray-600"
          }`}
        >
          Join our life-saving community
        </p>

        <div
          className={`mt-8 ${
            isDarkMode ? "bg-gray-800/80" : "bg-white/80"
          } backdrop-blur-sm py-8 px-6 shadow-2xl rounded-2xl`}
        >
          <form onSubmit={handleSubmit} className="space-y-6">
            <div className="grid grid-cols-1 gap-6 md:grid-cols-2">
              <div>
                <label
                  className={`block text-sm font-medium ${
                    isDarkMode ? "text-gray-300" : "text-gray-700"
                  }`}
                >
                  First Name
                </label>
                <InputField
                  icon={User}
                  name="firstName"
                  placeholder="Enter your first name"
                  required
                />
              </div>

              <div>
                <label
                  className={`block text-sm font-medium ${
                    isDarkMode ? "text-gray-300" : "text-gray-700"
                  }`}
                >
                  Last Name
                </label>
                <InputField
                  icon={User}
                  name="lastName"
                  placeholder="Enter your last name"
                  required
                />
              </div>

              <div>
                <label
                  className={`block text-sm font-medium ${
                    isDarkMode ? "text-gray-300" : "text-gray-700"
                  }`}
                >
                  Email
                </label>
                <InputField
                  icon={Mail}
                  name="email"
                  type="email"
                  placeholder="Enter your email"
                  required
                />
                {emailError && (
                  <p className="mt-1 text-sm text-red-500">{emailError}</p>
                )}
              </div>

              <div>
                <label
                  className={`block text-sm font-medium ${
                    isDarkMode ? "text-gray-300" : "text-gray-700"
                  }`}
                >
                  Phone Number
                </label>
                <InputField
                  icon={Phone}
                  name="phone"
                  type="tel"
                  placeholder="Enter your phone number"
                  required
                />
              </div>

              <div>
                <label
                  className={`block text-sm font-medium ${
                    isDarkMode ? "text-gray-300" : "text-gray-700"
                  }`}
                >
                  Password
                </label>
                <div className="relative">
                  <InputField
                    icon={Lock}
                    name="password"
                    type={showPassword ? "text" : "password"}
                    placeholder="Create a password"
                    required
                  />
                  <button
                    type="button"
                    onClick={() => setShowPassword(!showPassword)}
                    className="absolute inset-y-0 right-0 pr-3 flex items-center"
                  >
                    {showPassword ? (
                      <EyeOff className="h-5 w-5 text-gray-400 hover:text-gray-500" />
                    ) : (
                      <Eye className="h-5 w-5 text-gray-400 hover:text-gray-500" />
                    )}
                  </button>
                </div>
                {(focusedInput === "password" || formData.password) && (
                  <div className="mt-2 space-y-2">
                    <PasswordCriteria
                      met={passwordErrors.length}
                      text="8-12 characters long"
                    />
                    <PasswordCriteria
                      met={passwordErrors.number}
                      text="Contains a number"
                    />
                    <PasswordCriteria
                      met={passwordErrors.uppercase}
                      text="Contains an uppercase letter"
                    />
                    <PasswordCriteria
                      met={passwordErrors.lowercase}
                      text="Contains a lowercase letter"
                    />
                    <PasswordCriteria
                      met={passwordErrors.special}
                      text="Contains a special character"
                    />
                  </div>
                )}
              </div>

              <div>
                <label
                  className={`block text-sm font-medium ${
                    isDarkMode ? "text-gray-300" : "text-gray-700"
                  }`}
                >
                  Confirm Password
                </label>
                <InputField
                  icon={Lock}
                  name="confirmPassword"
                  type="password"
                  placeholder="Confirm your password"
                  required
                />
                {!passwordMatch && formData.confirmPassword && (
                  <p className="mt-1 text-sm text-red-500">
                    Passwords do not match
                  </p>
                )}
              </div>

              <div>
                <label
                  className={`block text-sm font-medium ${
                    isDarkMode ? "text-gray-300" : "text-gray-700"
                  }`}
                >
                  Date of Birth
                </label>
                <InputField
                  icon={Calendar}
                  name="dateOfBirth"
                  type="date"
                  required
                />
              </div>

              <div>
                <label
                  className={`block text-sm font-medium ${
                    isDarkMode ? "text-gray-300" : "text-gray-700"
                  }`}
                >
                  Blood Type
                </label>
                <div className="relative">
                  <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                    <Drop
                      className={`h-5 w-5 ${
                        focusedInput === "bloodType"
                          ? "text-red-500"
                          : "text-gray-400"
                      }`}
                    />
                  </div>
                  <select
                    name="bloodType"
                    value={formData.bloodType}
                    onChange={handleInputChange}
                    onFocus={() => setFocusedInput("bloodType")}
                    onBlur={() => setFocusedInput("")}
                    className={`block w-full pl-10 pr-3 py-3 ${
                      isDarkMode
                        ? "bg-gray-700 text-white"
                        : "bg-white text-gray-900"
                    } rounded-lg focus:outline-none focus:ring-2 focus:ring-red-500 focus:ring-opacity-50 transition-all duration-200`}
                    required
                  >
                    <option value="">Select blood type</option>
                    {bloodTypes.map((type) => (
                      <option key={type} value={type}>
                        {type}
                      </option>
                    ))}
                  </select>
                </div>
              </div>

              <div>
                <label
                  className={`block text-sm font-medium ${
                    isDarkMode ? "text-gray-300" : "text-gray-700"
                  }`}
                >
                  Weight (kg)
                </label>
                <InputField
                  icon={AlertCircle}
                  name="weight"
                  type="number"
                  placeholder="Enter your weight"
                  min="45"
                  required
                />
              </div>

              <div>
                <label
                  className={`block text-sm font-medium ${
                    isDarkMode ? "text-gray-300" : "text-gray-700"
                  }`}
                >
                  Last Donation Date
                </label>
                <InputField icon={Calendar} name="lastDonation" type="date" />
              </div>
            </div>

            <div>
              <label
                className={`block text-sm font-medium ${
                  isDarkMode ? "text-gray-300" : "text-gray-700"
                }`}
              >
                Address
              </label>
              <textarea
                name="address"
                value={formData.address}
                onChange={handleInputChange}
                className={`mt-1 block w-full px-3 py-2 ${
                  isDarkMode
                    ? "bg-gray-700 text-white"
                    : "bg-white text-gray-900"
                } rounded-lg focus:outline-none focus:ring-2 focus:ring-red-500 focus:ring-opacity-50 transition-all duration-200`}
                rows="3"
                placeholder="Enter your full address"
                required
              />
            </div>

            <div>
              <label
                className={`block text-sm font-medium ${
                  isDarkMode ? "text-gray-300" : "text-gray-700"
                }`}
              >
                Medical Conditions (if any)
              </label>
              <textarea
                name="medicalConditions"
                value={formData.medicalConditions}
                onChange={handleInputChange}
                className={`mt-1 block w-full px-3 py-2 ${
                  isDarkMode
                    ? "bg-gray-700 text-white"
                    : "bg-white text-gray-900"
                } rounded-lg focus:outline-none focus:ring-2 focus:ring-red-500 focus:ring-opacity-50 transition-all duration-200`}
                rows="3"
                placeholder="List any medical conditions or medications"
              />
            </div>

            <button
              type="submit"
              className={`w-full flex justify-center py-3 px-4 border border-transparent rounded-lg shadow-sm text-sm font-medium text-white ${
                isDarkMode
                  ? "bg-gradient-to-r from-red-700 to-red-600"
                  : "bg-gradient-to-r from-red-600 to-red-500"
              } hover:from-red-700 hover:to-red-600 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-red-500 transition-all duration-200`}
            >
              Register as Donor
            </button>
          </form>
        </div>
      </div>
    </div>
  );
};

const PasswordCriteria = ({ met, text }) => (
  <div className="flex items-center gap-2">
    <div
      className={`w-2 h-2 rounded-full ${met ? "bg-green-500" : "bg-red-500"}`}
    />
    <span className="text-sm text-gray-600">{text}</span>
  </div>
);

export default DonorRegistration;
